<template>
  <div>
		<!-- loading -->
    <loading :active.sync="isLoading"></loading>
  
    <!-- container -->
    <div class="container cart-grid">
      <!-- row -->
      <div class="row">
        <!--  -->
        <div class="col-md-7 row justify-content-center">
          <form class="col-md-6" @submit.prevent="createOrder()">
            <div class="form-group">
              <label for="useremail">Email</label>
              <!-- <input type="email" class="form-control" name="email" id="useremail"
                v-model="form.user.email" placeholder=" Email" required>
              <span class="text-danger"></span> -->
              <input type="email" class="form-control" name="email" id="useremail"
                v-validate="'required|email'"
                :class="{'is-invalid': errors.has('email')}"
                v-model="form.user.email" placeholder=" Email">
              <span class="text-danger" v-if="errors.has('email')">
                {{ errors.first('email') }}
              </span>
            </div>
          
            <div class="form-group">
              <label for="username"></label>
              <input type="text" class="form-control" name="name" id="username"
                v-validate="'required'" 
                :class="{'is-invalid':errors.has('name')}"
                v-model="form.user.name" placeholder="">
              <span class="text-danger" v-if="errors.has('name')"></span>
            </div>
          
            <div class="form-group">
              <label for="usertel"></label>
              <input type="tel" class="form-control" id="usertel" v-model="form.user.tel" placeholder="">
            </div>
          
            <div class="form-group">
              <label for="useraddress"></label>
              <input type="text" class="form-control" name="address" id="useraddress" 
                v-validate="'required'" 
                :class="{'is-invalid':errors.has('address')}"
                v-model="form.user.address"
                placeholder="">
              <span class="text-danger" v-if="errors.has('address')"></span>
            </div>
          
            <div class="form-group">
              <label for="comment"></label>
              <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
            </div>
            <div class="text-right">
              <button class="primary-btn order-submit"></button>
            </div>
          </form>
        </div>
        <!-- / -->
        <!--  -->
        <div class="col-md-5 justify-content-center">
          <div class="my-5 row justify-content-center">
            <table class="table">
              <thead>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </thead>
              <tbody>
                <tr v-for="item in cart.carts" :key="item.id" v-if="cart.carts">
                  <td class="align-middle">
                    <button type="button" @click.prevent="removeCartItem(item.id)" class="btn btn-outline-danger btn-sm">
                      <i class="far fa-trash-alt"></i>
                    </button>
                  </td>
                  <td class="align-middle">
                    {{ item.product.title }}
                    <div class="text-success" v-if="item.coupon">
                      
                    </div>
                  </td>
                  <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                  <td class="align-middle text-right">{{ item.final_total }}</td>
                </tr>
              </tbody>
              <tfoot>
                <tr>
                  <td colspan="3" class="text-right"></td>
                  <td class="text-right">{{ cart.total }}</td>
                </tr>
                <tr v-if="cart.final_total !== cart.total">
                  <td colspan="3" class="text-right text-success"></td>
                  <td class="text-right text-success">{{ cart.final_total }}</td>
                </tr>
              </tfoot>
            </table>
            <div class="input-group mb-3 input-group-sm">
              <input type="text" class="form-control" v-model="coupon_code" placeholder="">
              <div class="input-group-append">
                <button class="btn btn-outline-secondary" @click="addCouponCode()" type="button"></button>
              </div>
            </div>
          </div>
        </div>
        <!-- / -->
      </div>
      <!-- /row -->
    </div>
    <!-- /container -->

    
		
  </div>
</template>

<style lang="scss">
@import "../.././assets/all.scss"; //.scss

.tab-pane{
  padding-bottom: 100px;
}

.product-name{
  font-size: 20px;
}

.modal.fade {
    opacity: 0;
		&.show{
			opacity:1;
		}
	}
#page-nav-userhome{
  display: flex;
  align-items: center;
  justify-content: center;
}
.page-item.active .page-link {
    z-index: 1;
    color: #fff;
    background-color: #D10024;
    border-color:#D10024;
}

.shop:before,.shop:after{
	z-index: 1;
	opacity: 0.8;
}
.shop:before{
	width:33%;
}

.shop .shop-img{
	z-index: 0;
}
.shop .shop-body h3{
	text-align: left;
}
.shop .shop-body .cta-btn{
	text-align: left;
}

.fa-shopping-cart,
.header-ctn>div>a>span,
.dropdown-toggle::after{
	color:#FFFF;
}

</style>


<style>
  .navbar-nav{
    display: block;
    -webkit-box-orient: horizontal;
    font-size: 14px;
    }
  a {
    color: #2B2D42;
    font-weight: 500;
    }
</style>

<script>
import $ from 'jquery'; //jQuery
import { ValidationProvider } from 'vee-validate';
export default {
  data() {
    return {
      products: [],
			product: {},
			pagination:[], //
      status: {
        loadingItem: '', //
      },
      form:{
        user:{
           name:'',
          email:'',
          tel:'',
          address:'',
        },
        message:'',
      },
			cart:{},
      isLoading: false,
      coupon_code:'',
    };
  },
  methods: {
    // getProducts() {
    //   const vm = this;
    //   const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products`;
    //   vm.isLoading = true;
    //   this.$http.get(url).then((response) => {
    //     vm.products = response.data.products;
    //     console.log("getProducts",response);
    //     vm.isLoading = false;
    //   });
		// },
		getProducts(page = 1){ //ES6: page1
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`; //api
        const vm = this;
        vm.isLoading = true; //getProductsisLoading
        this.$http.get(api).then((response) => {
          console.log("getProducts:"+response.data);
          // if(response.data.success){
            vm.isLoading = false; //getProductsisLoading
            vm.products = response.data.products;
            vm.pagination = response.data.pagination;
          // }
        });
      },
    getProduct(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      vm.status.loadingItem = id;
      this.$http.get(url).then((response) => {
        vm.product = response.data.product;
        $('#productModal').modal('show'); //modal
        console.log("getProduct",response);
        vm.status.loadingItem = '';//loadingItemid
      });
    },
    addtoCart(id,qty = 1){
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.status.loadingItem = id;
      const cart = {
        product_id : id,
        qty,
      };
      this.$http.post(url, {data: cart} ).then((response) => {
        $('#productModal').modal('hide'); //modal
				console.log("",response);
        vm.getCart();
				vm.status.loadingItem = '';//loadingItemid
      });
    },
    getCart(){
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
				vm.cart = response.data.data;
        console.log("",response);
        vm.isLoading = false;
      });
		},
    removeCartItem(id){
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(url).then((response) => { //delete
        vm.getCart();
        console.log("",response);
        vm.isLoading = false;
      });
    },
    addCouponCode(){
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
      const coupon = { //couponcouponCode
        code : vm.coupon_code,
      };
      vm.isLoading = true;
      this.$http.post(url, {data:coupon}).then((response) => { //postdata
        vm.getCart();
        console.log("",response);
        vm.isLoading = false;
      });
    },
    createOrder(){
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const order = vm.form;
      vm.isLoading = true;
       this.$validator.validate().then((result) => {
        if (result) {
          this.$http.post(url, { data: order }).then((response) => {
            console.log('', response);
            if(response.data.success){ //
              vm.$router.push(`/customer_checkout/${response.data.orderId}`);//customer_checkout
            };
            vm.isLoading = false;
          });
        } else {
          console.log('');
        }
      });
      // this.$http.post(url, {data:order}).then((response) => { //postdata
      //   // vm.getCart();
      //   console.log("",response);
      //   vm.isLoading = false;
      // });
    },
  },
  created() {
    this.getProducts();
		this.getCart();
  },
};
</script>







